  font-size: 18px;
  box-shadow: 2px 2px 4px 1px rgba(46, 143, 213, .227), 1px 1px 1px rgba(91, 165, 161, .14), 2px 2px 1px -1px rgba(47, 12, 142, .21);
}

.cat-thomb {
