  display: flex;
}

.temperament {
  color: #20f;
  font-size: 10px;
  font-weight: bold;
}

.title {
  color: #20f;
  font-size: 15px;
  font-weight: bold;
}

.loader {
  width: 48px;
  height: 48px;
  background: #fff;
  border-radius: 50%;
  animation: roll 1s ease-in-out infinite alternate;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid #20f;
  border-color: #20f rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes roll {
  0% {
    transform: translateX(-150%)rotate(0deg);
  }

  100% {
    transform: translateX(150%)rotate(360deg);
  }
}

